/**
 * @name: header
 */

import React from 'react';
import { Link } from 'gatsby';
import { Menu } from 'antd';

import styled from 'styled-components';

const Wrapper = styled(Menu)`
  margin: 0 auto;
  max-width: 960px;
`;

const Header = () => {
  return (
    <Wrapper mode="horizontal">
      <Menu.Item key="home">
        <Link to="/">首页</Link>
      </Menu.Item>
      <Menu.Item key="blogs">
      <Link to="/blogs">博客</Link>
      </Menu.Item>
    </Wrapper>
  );
}

export default Header
